<template>
  <div class="m-istyle">
    <dl @mouseover="move" :class="navList.class">
      <dt>{{navList.title}}</dt>
      <dd
        :class="{active:type == item.tab}"
        :data-type="item.tab"
        v-for="item in navList.list"
        :key="item.tab"
      >{{item.text}}</dd>
    </dl>

    <ul class="ibody">
      <li v-for="(item,index) in list[type]" :key="index">
        <el-card :body-style="{ padding: '0px' }" shadow="never">
          <img :src="item.image" class="image">
          <div class="cbody">
            <div class="title" :title="item.title">{{item.title}}</div>
            <div class="sub-title" :title="item.subTitle">{{item.subTitle}}</div>

            <div class="price-info">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">{{item.price}}</span>
                <span class="old-price">门市价¥{{item.price}}</span>
                <span class="sold bottom-right-info">{{item.address}}</span>
              </span>
            </div>
            <!-- <div class="price-info" v-if="item.rentNum && item.price_info.current_price">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">{{item.price_info.current_price}}</span>
              </span>
              <span class="old-price">门市价¥{{item.price_info.current_price}}</span>
              <span class="sold bottom-right-info">{{item.address}}</span>
            </div>-->
            <!-- 抢光 -->
            <!-- <div class="price-info" v-else-if="!item.rentNum"> 
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">抢光啦</span>
              </span>
            </div>
             <div class="price-info" v-else>
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">{{item.price_info.avg_price}}/<span class="units">{{item.price_info.units}}</span></span>

              </span>
              <span class="old-price">门市价¥{{item.price_info.current_price}}</span>
              <span class="sold bottom-right-info">{{item.address}}</span>
            </div>-->
          </div>
        </el-card>
      </li>
    </ul>
  </div>
</template>
<script>
import Api from "@/api/index.js";
const api = new Api();
export default {
  created() {
    api.getResults().then(res => {
      this.list = res.data.data;
    });
  },

  data() {
    return {
      type: "all",
      list: {}
    };
  },
  props: ["navList"],
  methods: {
    move(e) {
      let targetName = e.target.tagName.toLowerCase();
      if (targetName != "dd") {
        return;
      }
      this.type = e.target.dataset.type;
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>



