<template>
  <div class="m-menu">
    <dl class="nav">
      <dt>全部分类</dt>
      <dd v-for="item in list" :key="item.type" @mouseenter="listEnter(item)" @mouseleave="listLeave">
          <i class="food"></i>
          {{item.name}}
          <span class="arrow"></span>
      </dd>
      <div v-if="curDetali" class="detail" @mouseenter="detailEnter" @mouseleave="detailLeave">
           <template v-for="(item,index) in curDetali.items">
               <h4 :key="index">{{item.title}}</h4>
               <span v-for="(v,i) in item.items" :key="`${v}+${i}`">{{v}}</span>
           </template>
      </div>
    </dl>
  </div>
</template>


<script>
import Api from "@/api/index.js";
const api = new Api()
export default {
     created(){
       api.getNav().then((res)=>{
           this.list = res.data.data
       })
     },
     data(){
         return{
             isEnter:false,
             curDetali:null,
             list:[]
         }
     },

     methods:{
         listEnter(data){
             this.curDetali = data;
         },
         listLeave(){
              this.timer = setTimeout(()=>{
               this.curDetali = null;
              },2000)
         },
         detailEnter(){
              clearTimeout(this.timer)
         },
         detailLeave(){
            this.curDetali = null;
         },
     }
}
</script>



